*, *::before, *::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  min-height: 100vh;
}

img, svg, picture, video {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

h2 {
  font-size: 2rem;
  line-height: 1.2190625;
}

h3 {
  font-size: 1.125rem;
  line-height: 1.5555555556;
}

h4 {
  font-size: 4.5rem;
  line-height: 0.9861111111;
}

body {
  font-size: 0.9375rem;
  line-height: 1.8666666667;
  font-family: "Montserrat", sans-serif;
  color: rgb(110, 114, 142);
}

a {
  text-decoration: none;
  color: inherit;
}

.component__header {
  text-align: center;
}
.component__title {
  margin-bottom: 2.5rem;
}
.component__controls {
  border: none;
}

.pricing-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
}
.pricing-toggle label {
  color: rgba(110, 114, 142, 0.5);
  cursor: pointer;
}
.pricing-toggle input[type=radio] {
  display: inline-block;
  width: 50%;
  height: 100%;
  opacity: 0;
  position: relative;
  z-index: 1;
  cursor: pointer;
}
.pricing-toggle input[type=radio]:focus ~ .pricing-toggle__switcher {
  outline: 3px solid rgb(74, 77, 96);
}
.pricing-toggle__wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.5rem;
  height: 2rem;
  border-radius: 1rem;
  background-image: linear-gradient(135deg, rgb(105, 110, 221) 0%, rgb(162, 167, 240) 100%);
}
.pricing-toggle__switcher {
  display: block;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  z-index: 0;
  background-color: rgb(255, 255, 255);
  transition: all 250ms ease-out;
}
.pricing-toggle #monthly:checked ~ .pricing-toggle__switcher {
  right: 0.25rem;
  left: 50%;
}

body {
  background: rgb(246, 246, 254);
  padding-block: 4rem 4.4375rem;
}
@media (min-width: 45rem) {
  body {
    padding-block: 4.4375rem 6.4375rem;
  }
}